import React, { useState } from 'react';

import './Burger.css';
import RightNav from './RightNav';


const Burger = () => {

    const [ open, setOpen ] = useState(false)

    return (

        <>
            <div className={`${ open  ? `burger-active` : ''} burger-div`} onClick={() => setOpen(!open)}>
                <div />
                <div />
                <div />
            </div>
            <RightNav open={open}/>
        </>
    )
}

export default Burger;