import React from "react";

import './recipeCard.css';

const RecipeCard = ({ recipe }) => {


    return (
        <div className="recipe-card-div">

            <div>

                <img src={recipe.image} alt="chicken recipe" className="recipe-image"/>

                <div className="recipe-card-content">

                    <h1 className="recipe-title">{recipe.label}</h1>

                    <p className="recipe-details">Calories: {Math.round(recipe.calories)}kcal</p>

                    <p className="recipe-details">
                        Diet:
                        {
                            recipe.dietLabels.map((diet, index) => (
                            <span key={index}> {diet}</span>
                            ))
                        }
                    </p>

                    <ul className="recipe-ingre">
                        <p>Ingredients</p>
                        {
                            recipe.ingredients.map((ingredient, index) => (
                                <li key={index}>{ingredient.text}</li>
                            ))
                        }
                    </ul>

                </div>

            </div>
        </div>

    )
};

export default RecipeCard;