import React, { useState, useEffect } from 'react';


import './app.css';

import Header from './components/header-section/header';
import RecipeSearch from './components/recipe-search-section/recipeSearch';
import Loader from './components/loader-section/loader';
import RecipeCard from './components/recipe-card/recipeCard';

import Footer from './components/footer-section/footer'

function App() {
  //lets set default state for recipe
  const [ recipes, setRecipes ] = useState([]);

  //lets set the loading state
  const [ isLoading, setIsLoading ] = useState(true);

  //a search term state for the search recipe box
  const [ searchTerm, setSearchTerm ] = useState('chicken');

  //useEffect, we will configure the api outside the useEffect

  // const exampleUrl= "https://api.edamam.com/search?q=chicken&app_id=${YOUR_APP_ID}&app_key=${YOUR_APP_KEY}&from=0&to=3&calories=591-722&health=alcohol-free";
  const proxy = 'https://cors-anywhere.herokuapp.com/';
  const API_KEY = `e90210dc78c86acc61471c6bcb6b1bb3`;
  const APP_ID = `f83c5601`;

  //api endpoint
  const endPoint = `${proxy}api.edamam.com/search?q=${searchTerm}&app_id=${APP_ID}&app_key=${API_KEY}&from=0&to=30`;

  const getRecipes = async () => {
    const response = await fetch(endPoint);
    const data = await response.json();
    console.log(data);
    data.more ? setRecipes(data.hits): setRecipes([]);
    // setRecipes(data.hits);
    setIsLoading(false)
  };

  useEffect( () => {
    getRecipes();
  }, [searchTerm]);


  return (
    <div className="container">

      <Header/>
      
      <RecipeSearch searchRecipe={(text) => {setSearchTerm(text)} }/>

      <div className="intro">
        <p><span className="recipe">The Recipee</span> helps you get recipe for your favourite dish</p>
        <h3>Recipes for {searchTerm}</h3>
      </div>

      {!isLoading && recipes.length === 0 && <h1>No Recipe Found...</h1> }

      {
        isLoading ?
        <Loader/>
        : <div className="recipe-card">
          {
            recipes ?
            recipes.map(( recipe, index) => (
              <RecipeCard key={index} recipe={recipe.recipe}/>
            ))
            : "page not loading..."
          }
        </div>
      }
      <Footer/>
    
    </div>
  );
}

export default App;
